.scrollbar
{
	margin-left: 30px;
  width : 500px;
  padding : 10px 15px ;
  /*border : 1px solid black;*/
  margin : 10px auto ;
  background: #adeaf7;
  -webkit-box-shadow: 1px 1px 3px #a1a0a2;
          box-shadow: 1px 1px 3px #a1a0a2;
  height : 210px;
  border-radius: 5px;

  overflow-y: scroll;
}

#styleScrollbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

#styleScrollbar::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

#styleScrollbar::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #aeaffa;
}



