.listScrollbar
{
	margin: 0 0 0 20px;
  width : 280px;
  padding : 2px;
  /*border : 1px solid black;*/
  background: none;
  height : 130px;

  overflow-y: scroll;

}

#styleListScrollbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

#styleListScrollbar::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

#styleListScrollbar::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #aeaffa;
}



