.pagination {
  list-style: none;
  margin: 20px auto;
  padding : 10px 0;
  /*background: #f41b26;*/
  width : 800px;

}

.pagination li {
  display: inline;
  margin : 0 15px;
  color : white;
  border-radius: 50px;
  border : 2px solid white;
  padding : 5px 10px;
  font-size: 20px;
  font-weight: 800;
  width : 60px;
}

.pagination li:hover{

}

.pagination li a {
  text-decoration: none;
  color : white;
}

.pagination li a:hover{
  text-decoration: underline;
}

.pagination li a:focus{
  outline: none
}

.break-me {
  font-weight: 600;
  text-decoration: none;
}

.previous, .next {
  background-color: #0c79dd;
}

.active {
  background-color: black;
}



